export default "<template>\n  <div>\n    <div class=\"toolbar\">\n      <el-switch\n        v-model=\"options.escape\"\n        active-text=\"Sort Stable\"\n      />\n    </div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :data=\"data\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name'\n          },\n          {\n            title: 'Item Price',\n            field: 'price'\n          }\n        ]\n      ],\n      data: [\n        {\n          id: 1,\n          name: '<a href=\"#\">item 1</a>',\n          price: '$1'\n        }, {\n          id: 2,\n          name: '<a href=\"#\">item 2</a>',\n          price: '$2 '\n        }\n      ],\n      options: {\n        toolbar: '.toolbar',\n        escape: true,\n        height: 460\n      }\n    }\n  }\n}\n</script>\n"