export default "<template>\n  <div>\n    <div class=\"toolbar\">\n      <el-button @click=\"load\">\n        Load 10000 Rows\n      </el-button>\n      <el-button @click=\"append\">\n        Append 10000 Rows\n      </el-button>\n      Total rows: {{ total }}\n    </div>\n\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      total: 200,\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name'\n          },\n          {\n            title: 'Item Price',\n            field: 'price'\n          }\n        ]\n      ],\n      options: {\n        toolbar: '.toolbar',\n        height: 400,\n        virtualScroll: true,\n        showColumns: true\n      }\n    }\n  },\n  mounted () {\n    this.$refs.table.load(this.getData(20))\n  },\n  methods: {\n    getData (number, isAppend) {\n      if (!isAppend) {\n        this.total = 0\n      }\n      const data = [\n\n      ]\n      for (let i = this.total; i < this.total + number; i++) {\n        data.push({\n          'id': i,\n          'name': 'Item ' + i,\n          'price': '$' + i\n        })\n      }\n\n      if (isAppend) {\n        this.total += number\n      } else {\n        this.total = number\n      }\n\n      return data\n    },\n    load () {\n      this.$refs.table.load(this.getData(10000))\n    },\n    append () {\n      this.$refs.table.append(this.getData(10000, true))\n    }\n  }\n}\n</script>\n"