export default "<template>\n  <div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :data=\"data\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n\n      ],\n      data: [\n\n      ],\n      options: {\n        height: 400,\n        showColumns: true\n      }\n    }\n  },\n  mounted () {\n    this.buildTable(50, 50)\n  },\n  methods: {\n    buildTable (cells, rows) {\n      const columns = [\n\n      ]\n      const data = [\n\n      ]\n\n      for (let i = 0; i < cells; i++) {\n        columns.push({\n          field: 'field' + i,\n          title: 'Cell' + i,\n          sortable: true\n        })\n      }\n      for (let i = 0; i < rows; i++) {\n        const row = {\n\n        }\n        for (let j = 0; j < cells; j++) {\n          row['field' + j] = 'Row-' + i + '-' + j\n        }\n        data.push(row)\n      }\n\n      this.columns = columns\n      this.data = data\n    }\n  }\n}\n</script>\n"