export default "<template>\n  <div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :data=\"data\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id',\n            escape: true\n          },\n          {\n            title: 'Item Name',\n            field: 'name'\n          },\n          {\n            title: 'Item Price',\n            field: 'price'\n          }\n        ]\n      ],\n      data: [\n        {\n          id: '<b>Escape ID: 0</b>',\n          name: '<b>Bold Item</b>',\n          price: '$0'\n        }\n      ],\n      options: {\n        height: 460\n      }\n    }\n  }\n}\n</script>\n"