export default "<template>\n  <div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name',\n            searchFormatter: false,\n            formatter (value) {\n              return 'Formatted ' + value\n            }\n          },\n          {\n            title: 'Item Price',\n            field: 'price',\n            formatter (value) {\n              return 'Price: ' + value\n            }\n          }\n        ]\n      ],\n      options: {\n        height: 460,\n        search: true,\n        url: 'json/data1.json'\n      }\n    }\n  }\n}\n</script>\n"