export default "<template>\n  <div>\n    <div class=\"select\">\n      <el-select\n        v-model=\"options.locale\"\n        style=\"width: 100%\"\n      >\n        <el-option value=\"af-ZA\">\n          af-ZA\n        </el-option>\n        <el-option value=\"ar-SA\">\n          ar-SA\n        </el-option>\n        <el-option value=\"ca-ES\">\n          ca-ES\n        </el-option>\n        <el-option value=\"cs-CZ\">\n          cs-CZ\n        </el-option>\n        <el-option value=\"da-DK\">\n          da-DK\n        </el-option>\n        <el-option value=\"de-DE\">\n          de-DE\n        </el-option>\n        <el-option value=\"el-GR\">\n          el-GR\n        </el-option>\n        <el-option value=\"en-US\">\n          en-US\n        </el-option>\n        <el-option value=\"es-AR\">\n          es-AR\n        </el-option>\n        <el-option value=\"es-CL\">\n          es-CL\n        </el-option>\n        <el-option value=\"es-CR\">\n          es-CR\n        </el-option>\n        <el-option value=\"es-ES\">\n          es-ES\n        </el-option>\n        <el-option value=\"es-MX\">\n          es-MX\n        </el-option>\n        <el-option value=\"es-NI\">\n          es-NI\n        </el-option>\n        <el-option value=\"es-SP\">\n          es-SP\n        </el-option>\n        <el-option value=\"et-EE\">\n          et-EE\n        </el-option>\n        <el-option value=\"eu-EU\">\n          eu-EU\n        </el-option>\n        <el-option value=\"fa-IR\">\n          fa-IR\n        </el-option>\n        <el-option value=\"fi-FI\">\n          fi-FI\n        </el-option>\n        <el-option value=\"fr-BE\">\n          fr-BE\n        </el-option>\n        <el-option value=\"fr-FR\">\n          fr-FR\n        </el-option>\n        <el-option value=\"he-IL\">\n          he-IL\n        </el-option>\n        <el-option value=\"hr-HR\">\n          hr-HR\n        </el-option>\n        <el-option value=\"hu-HU\">\n          hu-HU\n        </el-option>\n        <el-option value=\"id-ID\">\n          id-ID\n        </el-option>\n        <el-option value=\"it-IT\">\n          it-IT\n        </el-option>\n        <el-option value=\"ja-JP\">\n          ja-JP\n        </el-option>\n        <el-option value=\"ka-GE\">\n          ka-GE\n        </el-option>\n        <el-option value=\"ko-KR\">\n          ko-KR\n        </el-option>\n        <el-option value=\"ms-MY\">\n          ms-MY\n        </el-option>\n        <el-option value=\"nb-NO\">\n          nb-NO\n        </el-option>\n        <el-option value=\"nl-NL\">\n          nl-NL\n        </el-option>\n        <el-option value=\"pl-PL\">\n          pl-PL\n        </el-option>\n        <el-option value=\"pt-BR\">\n          pt-BR\n        </el-option>\n        <el-option value=\"pt-PT\">\n          pt-PT\n        </el-option>\n        <el-option value=\"ro-RO\">\n          ro-RO\n        </el-option>\n        <el-option value=\"ru-RU\">\n          ru-RU\n        </el-option>\n        <el-option value=\"sk-SK\">\n          sk-SK\n        </el-option>\n        <el-option value=\"sv-SE\">\n          sv-SE\n        </el-option>\n        <el-option value=\"th-TH\">\n          th-TH\n        </el-option>\n        <el-option value=\"tr-TR\">\n          tr-TR\n        </el-option>\n        <el-option value=\"uk-UA\">\n          uk-UA\n        </el-option>\n        <el-option value=\"ur-PK\">\n          ur-PK\n        </el-option>\n        <el-option value=\"uz-Latn-UZ\">\n          uz-Latn-UZ\n        </el-option>\n        <el-option value=\"vi-VN\">\n          vi-VN\n        </el-option>\n        <el-option value=\"zh-CN\">\n          zh-CN\n        </el-option>\n        <el-option value=\"zh-TW\">\n          zh-TW\n        </el-option>\n      </el-select>\n    </div>\n\n    <div id=\"toolbar\">\n      <el-button\n        :disabled=\"deleteDisabled\"\n        @click=\"remove\"\n      >\n        <i class=\"el-icon-delete\" /> Delete\n      </el-button>\n    </div>\n    <div class=\"index\">\n      <element-table\n        ref=\"table\"\n        :columns=\"columns\"\n        :options=\"options\"\n        @on-check=\"onSelect\"\n        @on-uncheck=\"onSelect\"\n        @on-check-all=\"onSelect\"\n        @on-uncheck-all=\"onSelect\"\n      />\n    </div>\n  </div>\n</template>\n\n<script>\nimport $ from 'jquery'\nimport 'bootstrap-table/dist/bootstrap-table-locale-all'\n$.extend($.fn.bootstrapTable.defaults, $.fn.bootstrapTable.locales['en-US'])\n\nexport default {\n  data () {\n    return {\n      selections: [\n      ],\n      deleteDisabled: true,\n      columns: [\n        [\n          {\n            field: 'state',\n            checkbox: true,\n            rowspan: 2,\n            align: 'center',\n            valign: 'middle'\n          },\n          {\n            title: 'Item ID',\n            field: 'id',\n            rowspan: 2,\n            align: 'center',\n            valign: 'middle',\n            sortable: true,\n            footerFormatter: () => {\n              return 'Total'\n            }\n          },\n          {\n            title: 'Item Detail',\n            colspan: 3,\n            align: 'center'\n          }\n        ],\n        [\n          {\n            field: 'name',\n            title: 'Item Name',\n            sortable: true,\n            footerFormatter: data => {\n              return data.length\n            },\n            align: 'center'\n          },\n          {\n            field: 'price',\n            title: 'Item Price',\n            sortable: true,\n            align: 'center',\n            footerFormatter: data => {\n              return '$' + data.map(row => {\n                return +row.price.substring(1)\n              }).reduce((sum, i) => {\n                return sum + i\n              }, 0)\n            }\n          },\n          {\n            field: 'operate',\n            title: 'Item Operate',\n            align: 'center',\n            clickToSelect: false,\n            events: {\n              'click .like': (e, value, row) => {\n                alert('You click like action, row: ' + JSON.stringify(row))\n              },\n              'click .remove': (e, value, row) => {\n                this.$refs.table.remove({\n                  field: 'id',\n                  values: [\n                    row.id\n                  ]\n                })\n              }\n            },\n            formatter: () => {\n              return [\n                '<a class=\"like\" href=\"javascript:void(0)\" title=\"Like\">',\n                '<i class=\"el-icon-star-off\"></i>',\n                '</a>  ',\n                '<a class=\"remove\" href=\"javascript:void(0)\" title=\"Remove\">',\n                '<i class=\"el-icon-delete\"></i>',\n                '</a>'\n              ].join('')\n            }\n          }\n        ]\n      ],\n      options: {\n        locale: 'en-US',\n        toolbar: '#toolbar',\n        search: true,\n        showRefresh: true,\n        showToggle: true,\n        showFullscreen: true,\n        showColumns: true,\n        showColumnsToggleAll: true,\n        detailView: true,\n        showExport: true,\n        clickToSelect: true,\n        detailFormatter: (index, row) => {\n          const html = [\n          ]\n          for (const key in row) {\n            if (row[key]) {\n              html.push('<p><b>' + key + ':</b> ' + row[key] + '</p>')\n            }\n          }\n          return html.join('')\n        },\n        minimumCountColumns: 2,\n        showPaginationSwitch: true,\n        pagination: true,\n        idField: 'id',\n        pageList: '[10, 25, 50, 100, all]',\n        showFooter: true,\n        sidePagination: 'server',\n        url: 'https://examples.wenzhixin.net.cn/examples/bootstrap_table/data',\n        responseHandler: res => {\n          for (const row of res.rows) {\n            row.state = this.selections.includes(row.id)\n          }\n          return res\n        }\n      }\n    }\n  },\n  methods: {\n    onSelect () {\n      this.selections = this.$refs.table.getSelections().map(row => row.id)\n      this.deleteDisabled = !this.selections.length\n    },\n    remove () {\n      this.$refs.table.remove({\n        field: 'id',\n        values: this.selections\n      })\n      this.selections = [\n      ]\n    }\n  }\n}\n</script>\n\n<style>\n.index .like {\n  margin-right: 10px;\n}\n</style>\n"