export default "<template>\n  <div>\n    <div class=\"toolbar\">\n      <el-switch\n        v-model=\"options.sortStable\"\n        active-text=\"Sort Stable\"\n      />\n    </div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :data=\"data\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'Item ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name',\n            sortable: true\n          },\n          {\n            title: 'Item Price',\n            field: 'price'\n          }\n        ]\n      ],\n      data: [\n        {\n          id: 0,\n          name: 'test0',\n          price: '$0'\n        },\n        {\n          id: 1,\n          name: 'test0',\n          price: '$1'\n        },\n        {\n          id: 2,\n          name: 'test0',\n          price: '$2'\n        },\n        {\n          id: 3,\n          name: 'test0',\n          price: '$3'\n        },\n        {\n          id: 4,\n          name: 'test0',\n          price: '$4'\n        },\n        {\n          id: 5,\n          name: 'test0',\n          price: '$5'\n        },\n        {\n          id: 6,\n          name: 'test0',\n          price: '$6'\n        },\n        {\n          id: 7,\n          name: 'test0',\n          price: '$7'\n        },\n        {\n          id: 8,\n          name: 'test0',\n          price: '$8'\n        },\n        {\n          id: 9,\n          name: 'test0',\n          price: '$9'\n        },\n        {\n          id: 10,\n          name: 'test0',\n          price: '$10'\n        },\n        {\n          id: 11,\n          name: 'test0',\n          price: '$11'\n        },\n        {\n          id: 12,\n          name: 'test0',\n          price: '$12'\n        },\n        {\n          id: 13,\n          name: 'test0',\n          price: '$13'\n  