export default "<template>\n  <div>\n    <div id=\"toolbar\">\n      <el-button @click=\"checkBy\">\n        checkBy\n      </el-button>\n      <el-button @click=\"uncheckBy\">\n        uncheckBy\n      </el-button>\n    </div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            field: 'state',\n            checkbox: true\n          },\n          {\n            title: 'ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name'\n          },\n          {\n            title: 'Item Price',\n            field: 'price'\n          }\n        ]\n      ],\n      options: {\n        toolbar: '#toolbar',\n        height: 460,\n        url: 'json/data1.json'\n      }\n    }\n  },\n  methods: {\n    checkBy () {\n      this.$refs.table.checkBy({\n        field: 'id',\n        values: [\n          1, 2, 3\n        ]\n      })\n    },\n    uncheckBy () {\n      this.$refs.table.uncheckBy({\n        field: 'id',\n        values: [\n          1, 2, 3\n        ]\n      })\n    }\n  }\n}\n</script>\n"