export default "<template>\n  <div>\n    <div id=\"toolbar\">\n      <el-button @click=\"large\">\n        Large and resetView\n      </el-button>\n      <el-button @click=\"small\">\n        Small and resetView\n      </el-button>\n    </div>\n    <div\n      :style=\"{width: width + 'px'}\"\n      class=\"div-table\"\n    >\n      <element-table\n        ref=\"table\"\n        :columns=\"columns\"\n        :options=\"options\"\n      />\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      width: 600,\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name'\n          },\n          {\n            title: 'Item Price',\n            field: 'price'\n          }\n        ]\n      ],\n      options: {\n        toolbar: '#toolbar',\n        height: 460,\n        url: 'json/data1.json'\n      }\n    }\n  },\n  methods: {\n    large () {\n      this.width += 50\n      this.$refs.table.resetView()\n    },\n    small () {\n      this.width -= 50\n      this.$refs.table.resetView()\n    }\n  }\n}\n</script>\n"