export default "<template>\n  <div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'Item ID',\n            field: 'id'\n          },\n          {\n            title: '<i class=\"el-icon-star-on\"></i> Item Name',\n            field: 'name'\n          },\n          {\n            title: '<i class=\"el-icon-money\"></i> Item Price',\n            field: 'price'\n          }\n        ]\n      ],\n      options: {\n        url: 'json/data1.json',\n        height: 400,\n        rowStyle (row, index) {\n          const classes = [\n            'bg-blue',\n            'bg-green',\n            'bg-orange',\n            'bg-red'\n          ]\n\n          if (index % 2 === 0 && index / 2 < classes.length) {\n            return {\n              classes: classes[index / 2],\n              css: {\n                color: 'white'\n              }\n            }\n          }\n          return {\n            css: {\n              color: 'blue'\n            }\n          }\n        }\n      }\n    }\n  }\n}\n</script>\n\n<style>\n.bg-blue {\n  background: blue !important;\n}\n\n.bg-green {\n  background: green !important;\n}\n\n.bg-orange {\n  background: orange !important;\n}\n\n.bg-red {\n  background: red !important;\n}\n</style>\n"