export default "<template>\n  <div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id',\n            sortable: true\n          },\n          {\n            title: 'Item Name',\n            field: 'name',\n            sortable: true\n          },\n          {\n            title: 'Item Price',\n            field: 'price',\n            sortable: true\n          }\n        ]\n      ],\n      options: {\n        height: 460,\n        customSort (sortName, sortOrder, data) {\n          const order = sortOrder === 'desc' ? -1 : 1\n          data.sort(function (a, b) {\n            const aa = +((a[sortName] + '').replace(/[^\\d]/g, ''))\n            const bb = +((b[sortName] + '').replace(/[^\\d]/g, ''))\n            if (aa < bb) {\n              return order * -1\n            }\n            if (aa > bb) {\n              return order\n            }\n            return 0\n          })\n        },\n        url: 'json/data1.json'\n      }\n    }\n  }\n}\n</script>\n"