export default "<template>\n  <div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'Name',\n            field: 'github.name',\n            sortable: true\n          },\n          {\n            title: 'Stargazers',\n            field: 'github.count.stargazers',\n            sortable: true\n          },\n          {\n            title: 'Forks',\n            field: 'github.count.forks',\n            sortable: true\n          },\n          {\n            title: 'Description',\n            field: 'github.description',\n            sortable: true\n          }\n        ]\n      ],\n      options: {\n        flat: true,\n        search: true,\n        url: 'json/data3.json'\n      }\n    }\n  }\n}\n</script>\n"