export default "<template>\n  <div>\n    <div id=\"toolbar\">\n      <el-button @click=\"getVisibleColumns\">\n        getVisibleColumns\n      </el-button>\n      <el-button @click=\"getHiddenColumns\">\n        getHiddenColumns\n      </el-button>\n    </div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id',\n            sortable: true\n          },\n          {\n            title: 'Item Name',\n            field: 'name',\n            sortable: true,\n            visible: false\n          },\n          {\n            title: 'Item Price',\n            field: 'price',\n            sortable: true\n          }\n        ]\n      ],\n      options: {\n        toolbar: '#toolbar',\n        height: 460,\n        showColumns: true,\n        pagination: true,\n        sidePagination: 'server',\n        url: 'https://examples.wenzhixin.net.cn/examples/bootstrap_table/data'\n      }\n    }\n  },\n  methods: {\n    getVisibleColumns () {\n      this.$alert(this.$refs.table.getVisibleColumns().map(it => it.field), 'getVisibleColumns')\n    },\n    getHiddenColumns () {\n      this.$alert(this.$refs.table.getHiddenColumns().map(it => it.field), 'getHiddenColumns')\n    }\n  }\n}\n</script>\n"