export default "<template>\n  <div>\n    <div id=\"toolbar\">\n      <el-button @click=\"updateRow\">\n        updateRow\n      </el-button>\n    </div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name'\n          },\n          {\n            title: 'Item Price',\n            field: 'price'\n          }\n        ]\n      ],\n      options: {\n        toolbar: '#toolbar',\n        height: 428,\n        url: 'json/data1.json'\n      }\n    }\n  },\n  methods: {\n    updateRow () {\n      const randomId = 100 + ~~(Math.random() * 100)\n      this.$refs.table.updateRow({\n        index: 1,\n        row: {\n          id: randomId,\n          name: 'Item ' + randomId,\n          price: '$' + randomId\n        }\n      })\n    }\n  }\n}\n</script>\n"