export default "<template>\n  <div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id',\n            footerFormatter () {\n              return 'Total'\n            }\n          },\n          {\n            title: 'Item Name',\n            field: 'name',\n            footerFormatter (data) {\n              return data.length\n            }\n          },\n          {\n            title: 'Item Price',\n            field: 'price',\n            footerFormatter (data) {\n              const field = this.field\n              return '$' + data.map(function (row) {\n                return +row[field].substring(1)\n              }).reduce(function (sum, i) {\n                return sum + i\n              }, 0)\n            }\n          }\n        ]\n      ],\n      options: {\n        height: 460,\n        showFooter: true,\n        url: 'json/data1.json'\n      }\n    }\n  }\n}\n</script>\n"