export default "<template>\n  <div>\n    <el-form label-width=\"200px\">\n      <el-form-item label=\"paginationSuccessivelySize\">\n        <el-input-number\n          v-model=\"options.paginationSuccessivelySize\"\n          :min=\"1\"\n        />\n      </el-form-item>\n      <el-form-item label=\"paginationPagesBySide\">\n        <el-input-number\n          v-model=\"options.paginationPagesBySide\"\n          :min=\"0\"\n        />\n      </el-form-item>\n      <el-form-item label=\"paginationUseIntermediate\">\n        <el-switch v-model=\"options.paginationUseIntermediate\" />\n      </el-form-item>\n    </el-form>\n\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name'\n          },\n          {\n            title: 'Item Price',\n            field: 'price'\n          }\n        ]\n      ],\n      options: {\n        pagination: true,\n        sidePagination: 'server',\n        paginationVAlign: 'both',\n        paginationSuccessivelySize: 5,\n        paginationPagesBySide: 1,\n        paginationUseIntermediate: false,\n        url: 'https://examples.wenzhixin.net.cn/examples/bootstrap_table/data'\n      }\n    }\n  }\n}\n</script>\n"