export default "<template>\n  <div>\n    <div id=\"toolbar\">\n      <el-button @click=\"mergeCells\">\n        mergeCells\n      </el-button>\n    </div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name'\n          },\n          {\n            title: 'Item Price',\n            field: 'price'\n          }\n        ]\n      ],\n      options: {\n        toolbar: '#toolbar',\n        height: 400,\n        url: 'json/data1.json'\n      }\n    }\n  },\n  methods: {\n    mergeCells () {\n      this.$refs.table.mergeCells({\n        index: 1,\n        field: 'name',\n        colspan: 2,\n        rowspan: 3\n      })\n    }\n  }\n}\n</script>\n"