export default "<template>\n  <div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id',\n            detailFormatter (index, row) {\n              return index + '. Item ID: ' + row.id\n            }\n          },\n          {\n            title: 'Item Name',\n            field: 'name',\n            detailFormatter (index, row) {\n              return index + '. Item Name: ' + row.name\n            }\n          },\n          {\n            title: 'Item Price',\n            field: 'price',\n            detailFormatter (index, row) {\n              return index + '. Item Price: ' + row.price\n            }\n          }\n        ]\n      ],\n      options: {\n        height: 460,\n        detailView: true,\n        detailViewByClick: true,\n        detailViewIcon: false,\n        showColumns: true,\n        url: 'json/data1.json'\n      }\n    }\n  }\n}\n</script>\n"