export default "<template>\n  <div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            field: 'state',\n            checkbox: true\n          },\n          {\n            title: 'ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name',\n            formatter (value) {\n              return `<label><input type=\"checkbox\"> ${value}</label>`\n            }\n          },\n          {\n            title: 'Item Price',\n            field: 'price',\n            formatter (value) {\n              return `<button class=\"el-button el-button--mini\">${value}</button>`\n            }\n          }\n        ]\n      ],\n      options: {\n        height: 460,\n        clickToSelect: true,\n        url: 'json/data1.json',\n        ignoreClickToSelectOn (e) {\n          return [\n            'A',\n            'BUTTON',\n            'LABEL',\n            'INPUT'\n          ].includes(e.tagName)\n        }\n      }\n    }\n  }\n}\n</script>\n"