export default "<template>\n  <div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            field: 'state',\n            checkbox: true\n          },\n          {\n            title: 'ID',\n            field: 'id',\n            sortable: true\n          },\n          {\n            title: 'Item Name',\n            field: 'name',\n            sortable: true\n          },\n          {\n            title: 'Item Price',\n            field: 'price',\n            sortable: true\n          }\n        ]\n      ],\n      options: {\n        showColumns: true,\n        multipleSelectRow: true,\n        clickToSelect: true,\n        pagination: true,\n        search: true,\n        url: 'https://examples.wenzhixin.net.cn/examples/bootstrap_table/data',\n        sidePagination: 'server'\n      }\n    }\n  }\n}\n</script>\n"