export default "<template>\n  <div>\n    <div id=\"toolbar\">\n      <el-button @click=\"checkAll\">\n        checkAll\n      </el-button>\n      <el-button @click=\"uncheckAll\">\n        uncheckAll\n      </el-button>\n    </div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            field: 'state',\n            checkbox: true\n          },\n          {\n            title: 'ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name'\n          },\n          {\n            title: 'Item Price',\n            field: 'price'\n          }\n        ]\n      ],\n      options: {\n        toolbar: '#toolbar',\n        height: 460,\n        url: 'json/data1.json'\n      }\n    }\n  },\n  methods: {\n    checkAll () {\n      this.$refs.table.checkAll()\n    },\n    uncheckAll () {\n      this.$refs.table.uncheckAll()\n    }\n  }\n}\n</script>\n"