export default "<template>\n  <div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name',\n            cellStyle (value, row, index) {\n              const classes = [\n                'bg-blue',\n                'bg-green',\n                'bg-orange',\n                'bg-red'\n              ]\n\n              if (index % 2 === 0 && index / 2 < classes.length) {\n                return {\n                  classes: classes[index / 2],\n                  css: {\n                    color: 'white'\n                  }\n                }\n              }\n              return {\n                css: {\n                  color: 'blue'\n                }\n              }\n            }\n          },\n          {\n            title: 'Item Price',\n            field: 'price'\n          }\n        ]\n      ],\n      options: {\n        url: 'json/data1.json'\n      }\n    }\n  }\n}\n</script>\n\n<style>\n.bg-blue {\n  background: blue;\n}\n\n.bg-green {\n  background: green;\n}\n\n.bg-orange {\n  background: orange;\n}\n\n.bg-red {\n  background: red;\n}\n</style>\n"