export default "<template>\n  <div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'Name',\n            field: 'name'\n          },\n          {\n            title: 'Price',\n            field: 'price'\n          },\n          {\n            title: 'Columns1',\n            field: 'column1'\n          },\n          {\n            title: 'Columns2',\n            field: 'column2'\n          },\n          {\n            title: 'Columns3',\n            field: 'column3'\n          },\n          {\n            title: 'Columns4',\n            field: 'column4'\n          }\n        ]\n      ],\n      options: {\n        height: 460,\n        url: 'json/data5.json',\n        showColumns: true,\n        showColumnsToggleAll: true\n      }\n    }\n  }\n}\n</script>\n"