export default "<template>\n  <div>\n    <div id=\"toolbar\">\n      <el-button @click=\"getData\">\n        getData\n      </el-button>\n    </div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name'\n          },\n          {\n            title: 'Item Price',\n            field: 'price'\n          }\n        ]\n      ],\n      options: {\n        toolbar: '#toolbar',\n        height: 460,\n        sidePagination: 'server',\n        pagination: true,\n        url: 'https://examples.wenzhixin.net.cn/examples/bootstrap_table/data'\n      }\n    }\n  },\n  methods: {\n    getData () {\n      this.$alert(this.$refs.table.getData(), 'getData')\n    }\n  }\n}\n</script>\n"