export default "<template>\n  <div>\n    <div class=\"toolbar\">\n      <el-button>\n        Toolbar button example\n      </el-button>\n    </div>\n\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name'\n          },\n          {\n            title: 'Item Price',\n            field: 'price'\n          }\n        ]\n      ],\n      options: {\n        height: 460,\n        toolbar: '.toolbar',\n        toolbarAlign: 'right',\n        url: 'json/data1.json'\n      }\n    }\n  }\n}\n</script>\n"