export default "<template>\n  <div>\n    <div id=\"toolbar\">\n      <el-button @click=\"scrollToTop\">\n        scrollTo Top\n      </el-button>\n      <el-button @click=\"scrollToIndex\">\n        Scroll to row index:\n      </el-button>\n      <el-input-number\n        v-model=\"index\"\n        min=\"0\"\n      />\n      <el-button @click=\"scrollToBottom\">\n        scrollTo Bottom\n      </el-button>\n    </div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      index: 3,\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name'\n          },\n          {\n            title: 'Item Price',\n            field: 'price'\n          }\n        ]\n      ],\n      options: {\n        toolbar: '#toolbar',\n        height: 460,\n        url: 'json/data1.json'\n      }\n    }\n  },\n  methods: {\n    scrollToTop () {\n      this.$refs.table.scrollTo(0)\n    },\n    scrollToIndex () {\n      this.$refs.table.scrollTo({\n        unit: 'rows',\n        value: this.index\n      })\n    },\n    scrollToBottom () {\n      this.$refs.table.scrollTo('bottom')\n    }\n  }\n}\n</script>\n"