export default "<template>\n  <div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nimport $ from 'jquery'\n\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name'\n          },\n          {\n            title: 'Item Price',\n            field: 'price'\n          }\n        ]\n      ],\n      options: {\n        height: 460,\n        ajax (params) {\n          const url = 'https://examples.wenzhixin.net.cn/examples/bootstrap_table/data'\n          $.get(url + '?' + $.param(params.data)).then(res => {\n            params.success(res)\n          })\n        },\n        search: true,\n        sidePagination: 'server',\n        pagination: true\n      }\n    }\n  }\n}\n</script>\n"