export default "<template>\n  <div>\n    <div class=\"toolbar\">\n      <el-select\n        v-model=\"options.filterOptions.filterAlgorithm\"\n        class=\"form-control\"\n        name=\"filterAlgorithm\"\n      >\n        <el-option\n          value=\"and\"\n          selected\n        >\n          and\n        </el-option>\n        <el-option value=\"or\">\n          or\n        </el-option>\n      </el-select>\n      <el-button @click=\"filterBy\">\n        filterBy\n      </el-button>\n    </div>\n\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :data=\"data\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        {\n          title: 'ID',\n          field: 'id'\n        }, {\n          title: 'Item Name',\n          field: 'name'\n        }, {\n          title: 'Item Price',\n          field: 'price'\n        }\n      ],\n      data: [\n        {\n          id: 1,\n          name: 'Item 1',\n          price: '$1'\n        },\n        {\n          id: 2,\n          name: 'Item 2',\n          price: '$2'\n        },\n        {\n          id: 3,\n          name: 'Item 3',\n          price: '$1'\n        },\n        {\n          id: 4,\n          name: 'Item 4',\n          price: '$4'\n        },\n        {\n          id: 5,\n          name: 'Item 5',\n          price: '$5'\n        }\n      ],\n      options: {\n        toolbar: '.toolbar',\n        filterOptions: {\n          filterAlgorithm: 'and'\n        }\n      }\n    }\n  },\n  methods: {\n    filterBy () {\n      this.$refs.table.filterBy({\n        id: 3,\n        price: '$1'\n      })\n    }\n  }\n}\n</script>\n"