export default "<template>\n  <div>\n    <form @click.prevent=\"submit\">\n      <el-button type=\"submit\">\n        Submit\n      </el-button>\n\n      <element-table\n        ref=\"table\"\n        :columns=\"columns\"\n        :options=\"options\"\n      />\n    </form>\n  </div>\n</template>\n\n<script>\nimport $ from 'jquery'\n\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            radio: true\n          },\n          {\n            title: 'ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name'\n          },\n          {\n            title: 'Item Price',\n            field: 'price'\n          }\n        ]\n      ],\n      options: {\n        toolbar: '.btn-primary',\n        idField: 'id',\n        selectItemName: 'id',\n        height: 460,\n        url: 'json/data1.json'\n      }\n    }\n  },\n  methods: {\n    submit () {\n      this.$alert($('form').serialize(), 'Submit')\n    }\n  }\n}\n</script>\n"