export default "<template>\n  <div>\n    <div id=\"toolbar\">\n      <el-button @click=\"showRow\">\n        showRow\n      </el-button>\n      <el-button @click=\"hideRow\">\n        hideRow\n      </el-button>\n    </div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id',\n            sortable: true\n          },\n          {\n            title: 'Item Name',\n            field: 'name',\n            sortable: true\n          },\n          {\n            title: 'Item Price',\n            field: 'price',\n            sortable: true\n          }\n        ]\n      ],\n      options: {\n        toolbar: '#toolbar',\n        height: 460,\n        pagination: true,\n        sidePagination: 'server',\n        url: 'https://examples.wenzhixin.net.cn/examples/bootstrap_table/data'\n      }\n    }\n  },\n  methods: {\n    showRow () {\n      this.$refs.table.showRow({\n        index: 1\n      })\n    },\n    hideRow () {\n      this.$refs.table.hideRow({\n        index: 1\n      })\n    }\n  }\n}\n</script>\n"