export default "<template>\n  <div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        {\n          field: 'date',\n          title: 'Date Stamp',\n          sortable: true\n        }, {\n          field: 'm_type',\n          title: 'Type',\n          sortable: true\n        }, {\n          field: 'msdn',\n          title: 'MSDN',\n          sortable: true\n        }, {\n          field: 'file_name',\n          title: 'File Name',\n          sortable: true\n        }, {\n          field: 'message',\n          title: 'Message',\n          sortable: true\n        }\n      ],\n      options: {\n        url: 'json/353.json',\n        responseHandler (res) {\n          return res.messages\n        }\n      }\n    }\n  }\n}\n</script>\n"