export default "<template>\n  <div>\n    <div id=\"toolbar\">\n      <el-button @click=\"load\">\n        load\n      </el-button>\n    </div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name'\n          },\n          {\n            title: 'Item Price',\n            field: 'price'\n          }\n        ]\n      ],\n      options: {\n        toolbar: '#toolbar',\n        height: 460,\n        url: 'json/data1.json'\n      }\n    }\n  },\n  methods: {\n    randomData () {\n      const startId = ~~(Math.random() * 100)\n      const rows = [\n      ]\n\n      for (let i = 0; i < 10; i++) {\n        rows.push({\n          id: startId + i,\n          name: 'test' + (startId + i),\n          price: '$' + (startId + i)\n        })\n      }\n      return rows\n    },\n    load () {\n      this.$refs.table.load(this.randomData())\n    }\n  }\n}\n</script>\n"