export default "<template>\n  <div>\n    <div id=\"toolbar\">\n      <el-button @click=\"getRowByUniqueId\">\n        getRowByUniqueId\n      </el-button>\n    </div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name'\n          },\n          {\n            title: 'Item Price',\n            field: 'price'\n          }\n        ]\n      ],\n      options: {\n        toolbar: '#toolbar',\n        height: 460,\n        uniqueId: 'id',\n        url: 'json/data1.json'\n      }\n    }\n  },\n  methods: {\n    getRowByUniqueId () {\n      this.$alert(this.$refs.table.getRowByUniqueId(1), 'getRowByUniqueId')\n    }\n  }\n}\n</script>\n"