export default "<template>\n  <div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name'\n          },\n          {\n            title: 'Item Price',\n            field: 'price'\n          },\n          {\n            title: 'Item Price',\n            field: 'operate',\n            formatter: () => {\n              return `\n                <button class=\"like el-button\" title=\"Like\">\n                <i class=\"el-icon-star-off\"></i>\n                </button>\n                <button class=\"remove el-button\" title=\"Remove\">\n                <i class=\"el-icon-delete\"></i>\n                </button>\n              `\n            },\n            events: {\n              'click .like': (e, value, row) => {\n                this.$alert(`<pre>${JSON.stringify(row, null, 4)}</pre>`, 'You click like action', {\n                  dangerouslyUseHTMLString: true\n                })\n              },\n              'click .remove': (e, value, row) => {\n                this.$refs.table.remove({\n                  field: 'id',\n                  values: [\n                    row.id\n                  ]\n                })\n              }\n            }\n          }\n        ]\n      ],\n      options: {\n        height: 460,\n        url: 'json/data1.json'\n      }\n    }\n  }\n}\n</script>\n"