export default "<template>\n  <div>\n    <div class=\"toolbar\">\n      <el-switch\n        v-model=\"options.silentSort\"\n        active-text=\"Silent Sort\"\n      />\n    </div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id',\n            sortable: true\n          },\n          {\n            title: 'Item Name',\n            field: 'name',\n            sortable: true\n          },\n          {\n            title: 'Item Price',\n            field: 'price',\n            sortable: true\n          }\n        ]\n      ],\n      options: {\n        toolbar: '.toolbar',\n        silentSort: false,\n        height: 460,\n        pagination: true,\n        sidePagination: 'server',\n        url: 'https://examples.wenzhixin.net.cn/examples/bootstrap_table/data'\n      }\n    }\n  }\n}\n</script>\n"