export default "<template>\n  <div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name'\n          },\n          {\n            title: 'Item Price',\n            field: 'price'\n          }\n        ]\n      ],\n      options: {\n        height: 460,\n        detailView: true,\n        detailViewIcon: false,\n        detailViewByClick: true,\n        detailFormatter (index, row) {\n          const html = [\n          ]\n          for (const key in row) {\n            if (row[key]) {\n              html.push('<p><b>' + key + ':</b> ' + row[key] + '</p>')\n            }\n          }\n          return html.join('')\n        },\n        url: 'json/data1.json'\n      }\n    }\n  }\n}\n</script>\n"