export default "<template>\n  <div>\n    <div class=\"toolbar\">\n      <el-switch\n        v-model=\"options.visibleSearch\"\n        active-text=\"Visible Search\"\n      />\n    </div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :data=\"data\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name'\n          },\n          {\n            title: 'Item Price',\n            field: 'price',\n            visible: false\n          }\n        ]\n      ],\n      data: [\n        {\n          'id': 0,\n          'name': 'Item 0',\n          'price': '$0'\n        },\n        {\n          'id': 1,\n          'name': 'Item 1',\n          'price': '$1'\n        },\n        {\n          'id': 2,\n          'name': 'Item 2',\n          'price': '$2'\n        },\n        {\n          'id': 3,\n          'name': 'Item 3',\n          'price': '$3'\n        },\n        {\n          'id': 4,\n          'name': 'Item 4',\n          'price': '$4'\n        },\n        {\n          'id': 5,\n          'name': 'Item 5',\n          'price': '$5'\n        }\n      ],\n      options: {\n        toolbar: '.toolbar',\n        search: true,\n        visibleSearch: true\n      }\n    }\n  }\n}\n</script>\n"