export default "<template>\n  <div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id',\n            formatter: 'ID: %s'\n          },\n          {\n            title: 'Item Name',\n            field: 'name',\n            formatter (value, row) {\n              const icon = row.id % 2 === 0 ? 'fa-star' : 'fa-star-and-crescent'\n              return '<i class=\"fa ' + icon + '\"></i> ' + value\n            }\n          },\n          {\n            title: 'Item Price',\n            field: 'price',\n            formatter (value) {\n              // 16777215 == ffffff in decimal\n              const color = '#' + Math.floor(Math.random() * 6777215).toString(16)\n              return '<div style=\"color: ' + color + '\">' +\n      '<i class=\"fa fa-dollar-sign\"></i>' +\n      value.substring(1) +\n      '</div>'\n            }\n          }\n        ]\n      ],\n      options: {\n        height: 460,\n        url: 'json/data1.json'\n      }\n    }\n  }\n}\n</script>\n"