export default "<template>\n  <div>\n    <div id=\"toolbar\">\n      <el-button @click=\"expandAllRows\">\n        expandAllRows\n      </el-button>\n      <el-button @click=\"collapseAllRows\">\n        collapseAllRows\n      </el-button>\n    </div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name'\n          },\n          {\n            title: 'Item Price',\n            field: 'price'\n          }\n        ]\n      ],\n      options: {\n        toolbar: '#toolbar',\n        height: 460,\n        detailView: true,\n        detailFormatter (index, row) {\n          const html = [\n          ]\n          for (const [\n            key, value\n          ] of Object.entries(row)) {\n            html.push(`<p><b>${key}: </b>${value}</p>`)\n          }\n          return html.join('')\n        },\n        url: 'json/data1.json'\n      }\n    }\n  },\n  methods: {\n    expandAllRows () {\n      this.$refs.table.expandAllRows()\n    },\n    collapseAllRows () {\n      this.$refs.table.collapseAllRows()\n    }\n  }\n}\n</script>\n"