export default "<template>\n  <div>\n    <div class=\"toolbar\">\n      <el-radio\n        v-model=\"options.queryParamsType\"\n        label=\"limit\"\n      >\n        <code>limit</code>\n      </el-radio>\n      <el-radio\n        v-model=\"options.queryParamsType\"\n        label=\"\"\n      >\n        <code>''</code>\n      </el-radio>\n    </div>\n\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id',\n            sortable: true\n          },\n          {\n            title: 'Item Name',\n            field: 'name',\n            sortable: true\n          },\n          {\n            title: 'Item Price',\n            field: 'price',\n            sortable: true\n          }\n        ]\n      ],\n      options: {\n        toolbar: '.toolbar',\n        height: 460,\n        search: true,\n        pagination: true,\n        sidePagination: 'server',\n        queryParamsType: 'limit',\n        queryParams: params => {\n          this.$alert(`<pre>${JSON.stringify(params, null, 4)}</pre>`, 'queryParams', {\n            dangerouslyUseHTMLString: true\n          })\n          return params\n        },\n        url: 'https://examples.wenzhixin.net.cn/examples/bootstrap_table/data'\n      }\n    }\n  }\n}\n</script>\n"