export default "<template>\n  <div>\n    <div\n      id=\"toolbar\"\n      class=\"form-inline\"\n    >\n      <el-input-number\n        v-model=\"page\"\n        :min=\"1\"\n        :max=\"3\"\n      />\n      <el-button @click=\"selectPage\">\n        selectPage\n      </el-button>\n      <el-button @click=\"prevPage\">\n        prevPage\n      </el-button>\n      <el-button @click=\"nextPage\">\n        nextPage\n      </el-button>\n    </div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      page: 2,\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name'\n          },\n          {\n            title: 'Item Price',\n            field: 'price'\n          }\n        ]\n      ],\n      options: {\n        toolbar: '#toolbar',\n        height: 420,\n        pagination: true,\n        url: 'json/data1.json'\n      }\n    }\n  },\n  methods: {\n    selectPage () {\n      this.$refs.table.selectPage(this.page)\n    },\n    prevPage () {\n      this.$refs.table.prevPage()\n    },\n    nextPage () {\n      this.$refs.table.nextPage()\n    }\n  }\n}\n</script>\n"