export default "<template>\n  <div>\n    <div class=\"multiple-table-item\">\n      <element-table\n        ref=\"table\"\n        :columns=\"columns\"\n        :options=\"options\"\n      />\n    </div>\n\n    <div class=\"multiple-table-item\">\n      <element-table\n        ref=\"table\"\n        :columns=\"columns\"\n        :options=\"options\"\n      />\n    </div>\n\n    <div class=\"multiple-table-item\">\n      <element-table\n        ref=\"table\"\n        :columns=\"columns\"\n        :options=\"options\"\n      />\n    </div>\n\n    <div class=\"multiple-table-item\">\n      <element-table\n        ref=\"table\"\n        :columns=\"columns\"\n        :options=\"options\"\n      />\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'Bookmark 1',\n            field: 'github.name',\n            formatter (value) {\n              return [\n                '<div class=\"left\">',\n                '<a href=\"https://github.com/wenzhixin/' + value + '\" target=\"_blank\">' + value + '</a>',\n                '</div>',\n                '<div class=\"right\">',\n                '<a class=\"like\" href=\"javascript:void(0)\" title=\"Like\">',\n                '<i class=\"el-icon-star-off\"></i>',\n                '</a>  ',\n                '<a class=\"remove\" href=\"javascript:void(0)\" title=\"Remove\">',\n                '<i class=\"el-icon-delete\"></i>',\n                '</a>',\n                '</div>'\n              ].join('')\n            },\n            events: {\n              'click .like' (e, value, row) {\n                alert('You click like action, row: ' + JSON.stringify(row))\n              },\n              'click .remove' (e, value, row) {\n                alert('You click remove action, row: ' + JSON.stringify(row))\n              }\n            }\n          }\n        ]\n      ],\n      options: {\n        url: 'json/data3.json'\n      }\n    }\n  }\n}\n</script>\n\n<style lang=\"scss\">\n.multiple-table-item {\n  margin-bottom: 20px;\n\n  .left {\n    float: left;\n  }\n\n  .right {\n    float: right;\n  }\n\n  .like {\n    margin-right: 10px;\n  }\n}\n</style>\n"