export default "<template>\n  <div>\n    <div id=\"toolbar\">\n      <el-button @click=\"getHiddenRows\">\n        Hide and Get Hidden Rows\n      </el-button>\n      <el-button @click=\"showHiddenRows\">\n        Show Hidden Rows\n      </el-button>\n    </div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id',\n            sortable: true\n          },\n          {\n            title: 'Item Name',\n            field: 'name',\n            sortable: true\n          },\n          {\n            title: 'Item Price',\n            field: 'price',\n            sortable: true\n          }\n        ]\n      ],\n      options: {\n        toolbar: '#toolbar',\n        height: 460,\n        pagination: true,\n        sidePagination: 'server',\n        url: 'json/data2.json'\n      }\n    }\n  },\n  methods: {\n    getHiddenRows () {\n      this.$refs.table.hideRow({\n        index: 1\n      })\n      this.$alert(this.$refs.table.getHiddenRows(), 'getHiddenRows')\n    },\n    showHiddenRows () {\n      this.$refs.table.getHiddenRows(true)\n    }\n  }\n}\n</script>\n"