export default "<template>\n  <div>\n    <div id=\"toolbar\">\n      <el-button @click=\"updateCellByUniqueId\">\n        updateCellByUniqueId\n      </el-button>\n    </div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name'\n          },\n          {\n            title: 'Item Price',\n            field: 'price'\n          }\n        ]\n      ],\n      options: {\n        toolbar: '#toolbar',\n        height: 428,\n        uniqueId: 'id',\n        url: 'json/data1.json'\n      }\n    }\n  },\n  methods: {\n    updateCellByUniqueId () {\n      this.$refs.table.updateCellByUniqueId({\n        id: 3,\n        field: 'name',\n        value: 'Updated Name'\n      })\n    }\n  }\n}\n</script>\n"