export default "<template>\n  <div>\n    <div class=\"toolbar\">\n      Offset:\n      <el-input-number\n        v-model=\"params.offset\"\n        :min=\"0\"\n        :max=\"99\"\n      />\n      Limit:\n      <el-input-number\n        v-model=\"params.limit\"\n        :min=\"1\"\n        :max=\"99\"\n      />\n      <el-input\n        v-model=\"params.search\"\n        placeholder=\"Search\"\n      />\n      <el-button @click=\"submit\">\n        OK\n      </el-button>\n    </div>\n    <element-table\n      ref=\"table\"\n      :columns=\"columns\"\n      :options=\"options\"\n    />\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      params: {\n        offset: 0,\n        limit: 5,\n        search: ''\n      },\n      columns: [\n        [\n          {\n            title: 'ID',\n            field: 'id'\n          },\n          {\n            title: 'Item Name',\n            field: 'name'\n          },\n          {\n            title: 'Item Price',\n            field: 'price'\n          }\n        ]\n      ],\n      options: {\n        toolbar: '.toolbar',\n        height: 460,\n        showRefresh: true,\n        showToggle: true,\n        showColumns: true,\n        queryParams: () => {\n          return this.params\n        },\n        responseHandler (res) {\n          return res.rows\n        },\n        url: 'https://examples.wenzhixin.net.cn/examples/bootstrap_table/data'\n      }\n    }\n  },\n  methods: {\n    submit () {\n      this.$refs.table.refresh()\n    }\n  }\n}\n</script>\n\n<style lang=\"scss\" scoped>\n.el-input,\n.el-input-number {\n  width: 120px;\n}\n</style>\n"